<template>
  <!--      -->
  <div id="app">
    <van-sku
      v-model="orderShow"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :hide-stock="sku.hide_stock"
      :quota="quota"
      :quota-used="quotaUsed"
      :reset-stepper-on-hide="resetStepperOnHide"
      :reset-selected-sku-on-hide="resetSelectedSkuOnHide"
      :close-on-click-overlay="closeOnClickOverlay"
      :disable-stepper-input="disableStepperInput"
      :message-config="messageConfig"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
      v-order="this"
    />
  </div>
</template>
<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { ImagePreview } from "vant";
import { Toast } from "vant";
import { addShopCart,getShopCart } from "../../src/api/login";

export default {
  name: "buyInfo",
  data() {
    return {
      sku: {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: "30349", // skuValueId id
                name: "", // skuValueName
                imgUrl: "https://img.yzcdn.cn/1.jpg" // 
              },
              {
                id: "1215",
                name: "",
                imgUrl: "https://img.yzcdn.cn/2.jpg"
              },
              {
                id: "1315",
                name: "",
                imgUrl: "https://img.yzcdn.cn/2.jpg"
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: 1215, // skuId
            price: 50900, // 
            s1: "1215", //  k_s  s1  id
            stock_num: 110 //  sku 
          },
          {
            id: 30349, // skuId
            price: 50000, // 
            s1: "30349", //  k_s  s1  id
            stock_num: 10 //  sku 
          },
          {
            id: 1315, // skuId
            price: 40900, // 
            s1: "1315", //  k_s  s1  id
            stock_num: 40 //  sku 
          }
        ],
        price: "509.00", // 
        stock_num: 227, // 
        collection_id: 1215, //  skuId  collection_id sku  id
        none_sku: false, // 
        messages: [],
        hide_stock: true // 
      },

      goods: {
        // 
        title: "",
        //  sku 
        picture: "https://img.yzcdn.cn/1.jpg"
      },

      skuData: {
        //  id
        goodsId: "946755",
        selectedNum: 1
      },

      customStepperConfig: {
        // 
        // quotaText: 'xxx',
        // // 
        // handleOverLimit: (data) => {
        //   const { action, limitType, quota, quotaUsed } = data;
        //   if (action === 'minus') {
        //     Toast('');
        //   } else if (action === 'plus') {
        //     const { LIMIT_TYPE } = Sku.skuConstants;
        //     if (limitType === LIMIT_TYPE.QUOTA_LIMIT) {
        //       let msg = `${quota}`;
        //       if (quotaUsed > 0) msg += `${quotaUsed}`;
        //       Toast(msg);
        //     } else {
        //       Toast('~~');
        //     }
        //   }
        // }
      },

      messageConfig: {},
      goodsId: "946755",
      quota: 0,
      quotaUsed: 0,
      resetStepperOnHide: false,
      resetSelectedSkuOnHide: false,
      closeOnClickOverlay: false,
      disableStepperInput: false
    };
  },
  computed: {
    ...mapState({
      buyImg: state => state.home.buyInfo.img[0],
      actives: state => state.active.home.title,
      shop_info: state => state.home.shop_info,
      my_info: state => state.home.my_info,
      orderShow: state => state.home.orderShow
    })
  },
  mounted() {
    this.detial = JSON.parse(localStorage.getItem("detial_s"));
    this.setdata();
  },
  methods: {
    ...mapActions(["orderNum"]),
    ...mapActions(["orderShows"]),
    ...mapActions(["infoAction"]),
    search_shows() {
      this.$router.back(-1);
    },
    setdata() {
      this.sku.stock_num = this.detial.saleNum;
      this.sku.price = this.detial.specialPrice;
      this.goods.title = this.detial.productName;
      this.goods.picture =
        "http://" + this.detial.productUrl + this.detial.productImage;
      let newv = [];
      let newlist = [];
      var str = this.detial.saleColor;
      var datas = new Array();
      datas = str.split("");
      for (var i in datas) {
        const element = datas[i];
        newv.push({
          id: i,
          imgUrl: this.goods.picture,
          name: element
        });
        newlist.push({
          id: i,
          s1: i,
          price: this.detial.specialPrice * 100,
          stock_num: 100
        });
      }
      this.sku.tree[0].v = newv;
      this.sku.list = newlist;
    },
    //
    ImagePreviews() {
      ImagePreview(this.preImgs);
    },
    onBuyClicked(data) {
      const sel = document.querySelector(".van-stepper__input");
      const color = document.querySelector(
        ".van-sku-row__item.van-sku-row__item--active"
      );
      this.orderNum(sel.value);
      var selIn = [];
      selIn.push({
        orderNum: sel.value,
        color: color.innerHTML
      });
      localStorage.setItem("selIn", JSON.stringify(selIn));
      this.$router.push("/goods/id_2/buy/pay");
    },
    onAddCartClicked(data) {
      const sel = document.querySelector(".van-stepper__input");
      const color = document.querySelector(
        ".van-sku-row__item.van-sku-row__item--active"
      );
      var selIn = [];
      selIn.push({
        orderNum: sel.value,
        color: color.innerHTML
      });
      var str = selIn[0].color.replace(/\s+/g,"");
      var addShopData = {
        productId: this.detial.id,
        productNum: selIn[0].orderNum,
        price: this.detial.specialPrice,
        productName: this.detial.productName,
        productColor: str,
        productImage:this.goods.picture
      } 
      let para = {
        token: JSON.parse(localStorage.getItem("token")),
        shopCart: JSON.stringify(addShopData)
      };
      addShopCart(para).then(res => {
         this.getShopCart1()
          Toast("");
          this.orderShows();
      });
    },
    getShopCart1(){
   let para = {
      token: JSON.parse(localStorage.getItem("token"))
    };
    getShopCart(para).then(res => {
       localStorage.setItem('getShopCarts', JSON.stringify(res.shopCart))
       this.infoAction()
       this.info = JSON.parse(localStorage.getItem('getShopCarts')).length;
    });
   }
    
  },
     
  watch: {},
  directives: {
    order: {
      inserted(el, e) {
        let data = JSON.parse(localStorage.getItem("detial_s"));
        const orderImg = el.childNodes[0];
        const urlImg = "http://" + data.productUrl + data.productImage;
        const orderChoose = el.childNodes[1];
        orderImg.childNodes[0].innerHTML = `<img src=${urlImg}> `;
        orderImg.childNodes[0].childNodes[0].style.width = "100%";
      }
    }
  },
  beforeCreate() {}
};
</script>
<style scoped>
#app >>> .van-button--bottom-action {
  border-radius: 24px 24px 24px 24px;
}
#app >>> .van-button--bottom-action {
  height: 40px;
  line-height: 40px;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
}
#app
  .van-button.van-button--default.van-button--normal.van-button--bottom-action {
  background-color: #b39061;
  border: 1px solid #b39061;
}
</style>




